import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';

import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { BlinkId } from '@ionic-native/blinkid/ngx';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { PerfilPopover } from './perfil/perfil.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TooltipModule } from 'ngx-bootstrap/tooltip';


import localeEs from '@angular/common/locales/es-CL';
import localeEsExtra from '@angular/common/locales/extra/es-CL';
import { registerLocaleData } from '@angular/common';

// STORAGE 
import { IonicStorageModule } from '@ionic/storage';


registerLocaleData(localeEs, 'Es', localeEsExtra);

@NgModule({
  declarations: [AppComponent, PerfilPopover],
  entryComponents: [PerfilPopover],
  imports: [
    TooltipModule.forRoot(),
    BrowserModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    BrowserAnimationsModule,
    IonicStorageModule.forRoot()
  ],
  providers: [
    StatusBar,
    SplashScreen,
    BlinkId,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
