import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./rendidor/rendidor.module').then(m => m.RendidorPageModule)
  },
  {
    path: 'aprobador',
    loadChildren: () => import('./aprobador/aprobador.module').then(m => m.AprobadorPageModule)
  },
  {
    path: 'administrador',
    loadChildren: () => import('./administrador/administrador.module').then(m => m.AdministradorPageModule)
  },
  { path: 'editar', loadChildren: './rendidor/gastos/editar/editar.module#EditarPageModule' },  { path: 'detalle', loadChildren: './rendidor/galeria/detalle/detalle.module#DetallePageModule' },




];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
