import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'app-perfil',
    template: `
    <ion-list no-margin lines="none" mode="ios">
        <ion-list-header mode="ios">Perfiles disponibles</ion-list-header>
        <ion-radio-group (ionChange)="changePerfil($event)" mode="ios">
            <ion-item *ngFor="let item of radio_list" mode="ios">
                <ion-icon src="assets/icons/{{ item.icon }}.svg" slot="start" mode="ios"></ion-icon>
                <ion-label mode="ios"> {{ item.text }}</ion-label>
                <ion-radio mode="ios" slot="end" [value]="item.value" [checked]="item.checked"></ion-radio>
            </ion-item>
        </ion-radio-group>
        <ion-button expand="block" mode="ios">Cerrar Sesion</ion-button>
    </ion-list>
  `
})
export class PerfilPopover implements OnInit {
    id_perfil: string;

    radio_list = [
        {
            id: '1',
            name: 'radio_list',
            value: '1',
            text: 'Rendidor',
            icon: 'user-solid',
            disabled: false,
            checked: false,
            color: 'primary'
        }, {
            id: '2',
            name: 'radio_list',
            value: '2',
            text: 'Aprobador',
            icon: 'user-check-solid',
            disabled: false,
            checked: false,
            color: 'primary'
        }, {
            id: '3',
            name: 'radio_list',
            value: '3',
            text: 'Administrador',
            icon: 'user-tie-solid',
            disabled: false,
            checked: false,
            color: 'primary'
        }, {
            id: '4',
            name: 'radio_list',
            value: '4',
            text: 'Contador',
            icon: 'user-edit-solid',
            disabled: false,
            checked: false,
            color: 'primary'
        },
    ];


    constructor(private router: Router) {
    }

    ngOnInit() {
        this.id_perfil = localStorage.getItem('perfil');
        this.radio_list
            .filter(val => val.value === this.id_perfil)
            .map(val => val.checked = true);
    }

    public changePerfil(e) {
        const id = Number(e.detail.value);
        if (id === 1) {
            localStorage.setItem('perfil', '1');
            this.router.navigate(['/rendidor']);
        } else if (id === 2) {
            localStorage.setItem('perfil', '2');
            this.router.navigate(['/aprobador']);
        } else if (id === 3) {
            localStorage.setItem('perfil', '3');
            this.router.navigate(['administrador']);
        }
    }

}